import React, {useState} from 'react';
import axios from 'axios';

const LOCAL_STORAGE_KEY = "location";
function saveToLocalStorage(location) {
  localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(location));
}

function readFromLocalStorage() {
  const storedLocations = localStorage.getItem(LOCAL_STORAGE_KEY);
  return storedLocations ? JSON.parse(storedLocations) : [];
}
function Weather() {
  const [data, setData] = useState(readFromLocalStorage());
  const [location, setLocation] = useState(readFromLocalStorage());

  // API Key and URL
  const apiKey = process.env.REACT_APP_WEATHER_API_KEY;
  const url = `https://api.openweathermap.org/data/2.5/weather?q=${location}&units=imperial&appid=${apiKey}`

  const searchLocation = (event) => {
    if (event.key === 'Enter') {
      axios.get(url).then((response) => {
        setData(response.data)
        console.log(response.data)
      })
      setLocation('');
      saveToLocalStorage(location);
    }
  }
  return (
    <div className="weather-background">
      <h1 className="weather-h1">
        Weather</h1>
      <div className="weather-container">

        <div className="weather-search">
          <input type="text"
            value={location}
            onChange={
              event => setLocation(event.target.value)
            }
            onKeyPress={searchLocation}
            placeholder="Enter Location"/>
        </div>
        <div className="top">
          <div className="location">
            <p> {
              data.name
            } </p>
          </div>
          <div className="temp">
            {
            data.main ? <h2 className="weather-h2">
              {
              data.main.temp.toFixed()
            }℉</h2> : null
          } </div>
          <div className="description">
            {
            data.weather ? <p>{
              data.weather[0].main
            }</p> : null
          } </div>
        </div>
        <div className="bottom">
          <div className="feels">
            {
            data.main ? <p className="bold">
              {
              data.main.feels_like.toFixed()
            }℉</p> : null
          }
            <p>
              Feels Like</p>
          </div>
          <div className="humidity">
            {
            data.main ? <p className="bold">
              {
              data.main.humidity
            }%</p> : null
          }
            <p>
              Humidity</p>
          </div>
          <div className="wind">
            {
            data.wind ? <p className='bold'>
              {
              data.wind.speed.toFixed()
            }
              MPH</p> : null
          }
            <p>
              Wind</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Weather;
